@import "../../global.scss";
.menu{
    width: 300px;
    height: 100vh;
    background-color: $mainColor;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    z-index: 2;
    position: fixed;
    top: 70px;
    right:-300px;
    transition: all 1s ease;
    &.active{
        right: 0;
    }
    ul{
        font-size: 30px;
        margin: 0;
        padding: 0;
        list-style: none;
        color: white;
        font-weight: 300;
        width: 60%;
        li{
            margin-bottom: 25px;
            a{
                text-decoration: none;
                color: inherit;

            }
            &:hover{
                font-weight: 500;
            }
        }
    }
}