.app{
    height: 100vh;
    .section {
        width: 100%;
        height: calc(100vh - 70px);
        background-color: lightcoral;
        position: relative;
        top: 70px;
        scroll-snap-type: y mandatory;
        scroll-behavior: smooth;
        &::-webkit-scrollbar{
            display: none;
        }
        >* {
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }

    }
}