.works{
    background-color: rgb(228, 63, 96);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    .arrow{
        position: absolute;
        width: 50px;
        cursor: pointer;
        &.left{
            left: 100px;
            transform: rotate(-180deg);
        }
        &.right{
            right: 100px;

        }
    }
    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease;
        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;
            .item{
                width: 700px;
                height: 100%;
                background-color: white;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius:20px;
                .left{
                    flex: 4;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    .leftContainer{
                        width: 90%;
                        height: 70%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        .imgContainer{
                            width: 40px;
                            height: 40px;
                            border-radius: 50%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            background-color:  rgb(244, 170, 143);
                            img{
                                width: 25px;
                            }
                           
                        }
                        h2{
                            font-size: 20px;
                        }
                        p{
                            font-size: 13px;
                        }
                        span{
                            font-size: 12px;
                            font-weight: 600;
                            text-decoration: underline;
                            cursor: pointer;
                        }
                    }
                }
                .right{
                    flex: 8;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    overflow: hidden;
                    img{
                        width: 400px;
                        transform: rotate(-20deg);
                    }
                }
            }
        }
    }
}
