@import "../../global.scss";
.portfolio{
    background-color: white;
   display: flex;
   flex-direction: column;
   align-items: center;
   h1{
    font-size: 50px;
   }
   ul{
    display: flex;
    margin: 10px;
    list-style: none;
    padding: 0;
    .portfolioList{
        font-size: 14px;
        margin-right: 50px;
        padding: 7px;
        border-radius: 10px;
        cursor: pointer;
       
        &.active{
           background-color: $mainColor;
           color: white;
        }
    }
   }
   .container{
    width: 68%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    .item{
        &::-webkit-scrollbar{
            display: none;
        }
        width: 220px;
        height: 150px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        border: 1px solid lightgrey;
        margin: 10px 20px;
        color: white;
        position: relative;
        cursor: pointer;
        transition: all 0.5s ease;
        h3{
            position: absolute;
            font-size: 20px;
        }
        img{
            width: 100%;
            height: 100%;
            object-fit: cover;
            z-index: 1;
        }
        &:hover{
            background-color: $mainColor;
            img{
                opacity: 0.5;
                z-index: 0;
            }
        }
    }
   }
}