@import "../../global.scss";
.topbar{
    width: 100%;
    background-color: white;
    height: 70px;
    color: $mainColor;
    transition: all 1s ease;
    position: fixed;
    top: 0;
    z-index: 2;
    .wrapper{
        padding: 10px 30px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        .left{
            display: flex;
            align-items: center;
            .logo{
                font-size: 40px;
                color: inherit;
                text-decoration: none;
                font-weight: 700;
                margin-right: 40px;
            }
            .itemcontainer{
                display: flex;
                align-items: center;
                margin-left: 30px;
                .icon{
                    font-size: 18px;
                    margin-right: 5px;
                }
                span{
                    font-size: 15px;
                    font-weight: 500;
                }
            }
        }
        .right{
            .humburger{
                height: 25px;
                width: 32px;
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                cursor: pointer;
                &::-webkit-scrollbar{
                    display: none;
                }
                span{
                    width: 100%;
                    height: 3px;
                    background-color: $mainColor;
                    transform-origin: left;
                    transition: all 2s ease;

                }
            }
        }
    }
    &.active{
        background-color: $mainColor;
        color: white;
        .humburger{
           span{
            &:first-child{
                background-color: white;
                transform: rotate(45deg);
            }
            &:nth-child(2){
                opacity: 0;
                
            }
            &:last-child{
                background-color: white;
                transform: rotate(-45deg);
            }
           }

        }
    }
}